.loading {
  filter: blur(3px);
}

.invoiceCard {
  display: flex;
  width: calc((100% - 60px) / 3);
  flex-grow: 1;
  min-width: 278px;
  flex-direction: column;
  background: white;
  border-radius: 20px;
  overflow: hidden;
  transition: all 0.1s ease;
  box-shadow: 0px 15px 15px -6px rgba(0, 0, 0, 0.1);
}
.invoiceCard header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background: #059669;
  box-sizing: border-box;
  padding: 15px;
  height: 80px;
  align-items: center;
  gap: 10px;
}
.invoiceCard header .user_picture {
  width: 50px;
  height: 50px;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 10px;
}
.invoiceCard header .username {
  color: white;
  flex: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.invoiceCard header .settings {
  position: relative;
  transition: all 0.3s ease;
  width: 35px;
  height: 35px;
  cursor: pointer;
}
.invoiceCard header .settings span {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  overflow: hidden;
  color: white;
  line-height: 1.9;
  font-size: 25px;
}
.invoiceCard header .settings span:hover {
  background: #10b981;
}
.invoiceCard main {
  padding: 15px;
  color: #6b7280;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
}
.invoiceCard main .invoice_row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px 0;
}
.invoiceCard main .invoice_row .amountUSD {
  color: #374151;
  display: flex;
  gap: 10px;
  align-items: center;
  flex-direction: row;
}
.invoiceCard main .invoice_row .status {
  font-size: 10px;
  border-radius: 10px;
  padding: 0 5px;
}
.invoiceCard main .invoice_row .paid {
  background: #ecfdf5;
  color: #10b981;
}
.invoiceCard main .invoice_row .overdue {
  background: #feebeb;
  color: #dd1c1a;
}
.invoiceCard main hr {
  height: 2px;
  width: 100%;
  background: #f3f4f6;
  border: none;
}/*# sourceMappingURL=InvoiceCard.css.map */